    const getTemplate = (data = [], placeholder) => {
    const text = placeholder ?? 'Placeholder –ø–æ —É–º–æ–ª—á–∞–Ω–∏—é –Ω–µ –∑–∞–¥–∞–Ω'

    const items = data.map(item => {
       return `<li class="select__item">${item.value}</li>`
    })

    return `
        <div class="select__input" data-type="input">
            <span>${text}</span>
            <i class="fa-solid fa-angle-down" data-type="arrow"></i>
        </div>
        <div class="select__dropdown">
            <ul class="select__list">
                ${items.join('')}
            </ul>
        </div>
    `
}

export class Select {
    constructor(selector, options) {
        this.$el = document.querySelector(selector)
        this.options = options

        this.#render()
        this.#setup()
    }

    #render() {
        const {placeholder, data} = this.options

        this.$el.classList.add('select')
        this.$el.innerHTML = getTemplate(data, placeholder)
    }

    #setup() {
        this.clickHandler = this.clickHandler.bind(this) // –¥–ª—è –∏—Å–ø–æ–ª—å–∑–æ–≤–∞–Ω–∏—è –Ω–∏–∂–µ –≤ —Å–æ—á–µ—Ç–∞–Ω–∏–∏ —Å this
        this.$el.addEventListener('click', this.clickHandler)
        this.$arrow = this.$el.querySelector('[data-type="arrow"]')
    }

    clickHandler(event) {
        const {type} = event.target.dataset
         if (type === 'input') {
            this.toggle()
         }
    }

    get isOpen() {
        return this.$el.classList.contains('open')
    }

    toggle() {
        this.isOpen ? this.close() : this.open() // –ø—Ä–æ–≤–µ—Ä–∫–∞ –Ω–∞ –æ—Ç–∫—Ä—ã—Ç–∏–µ –≤—ã–ø–∞–¥–∞—é—â–µ–≥–æ —Å–ø–∏—Å–∫–∞
    }

    open() {
        this.$el.classList.add('open')
        this.$arrow.classList.remove('fa-angle-down')
        this.$arrow.classList.add('fa-angle-up')
    }

    close() {
        this.$el.classList.remove('open')
        this.$arrow.classList.add('fa-angle-down')
        this.$arrow.classList.remove('fa-angle-up')
    }

    destroy() {
        this.$el.removeEventListener('click', this.clickHandler)
    }
}